/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-post-by-date {
	padding: 5%;
  	background-color: #953f3f24;
	opacity: initial !important;
}

#post-date {
	margin-left: 8%;
	width: 145px;
}

.post-list {
    display: flex;
    flex-direction: column;
    gap: 20px; // Espacio entre los elementos de la lista
}

.post-item {
	padding: 0px 15px;
	border: 1px solid #ccc;
	border-radius: 5px;
	background-color: #f9f9f9;
	margin: 15px;
}

.post-item h3 {
    margin-bottom: 10px;
    font-size: 2.3rem;
	margin-top: 1.25rem !important;
}

.load-more-button {
    display: block;
    margin: 20px auto;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #007cba;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #005a8e;
    }
}

.load-more-button:disabled {
    background-color: #999;
    cursor: not-allowed;
}

.load-more-posts {
	font-size: 1.2rem;
	margin-left: 3%;
}
